// Import partials
@import 'variables';
@import 'global';
@import 'font';
@import 'nav';
@import 'buttons';
@import 'hero';

:root {
    --max: 1920px;
}

body {
    margin: 0;
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', system-ui, sans-serif;
    background-color: #ffffff;
    color: #333333;
    position: relative;
}

.container {
    max-width: $small-container-max-width;
    margin: 0 auto;
    padding: 0 1.5rem;
}

.section {
    padding: 1.5rem 0;
}