.hero {
    background: $bg-gradient;
    height: 1079px;
    display: flex;
    align-items: center;


    @media (max-width: $bp-laptop) {
        height: auto;
        padding-top: 100px;
    }


    .text-box {
        max-width: 460px;

        @media (max-width: $bp-laptop) {
            margin: 0 auto;
            max-width: 100%;
        }

        h1 {
            font-size: 54px;
            letter-spacing: 0px;
            line-height: 109px;
            font-weight: 700;
            text-align: left;
            margin: 0;
            display: flex;
            gap: 15px;
            flex-direction: column;

            @media (max-width: $bp-laptop) {
                text-align: center;
                justify-content: center;
                align-items: center;

                font-size: clamp(2.5rem, 5vw, 3.375rem);
                line-height: 1.5;
            }


            span {
                border-radius: 10px;
                display: block;
                width: fit-content;
                padding: 2px;

                &:nth-of-type(1) {
                    background-color: $color-3;
                }

                &:nth-of-type(2) {
                    background-color: $color-1;
                }

                &:nth-of-type(3) {
                    background-color: $color-2;
                    margin-bottom: 57px;

                    @media (max-width: $bp-laptop) {
                        margin-bottom: 20px;
                    }
                }
            }
        }

        p {
            margin-bottom: 57px;

            @media (max-width: $bp-laptop) {
                text-align: center;
                margin-bottom: 20px;
            }

        }

    }

    .image-box {
        margin: 0 auto;
        max-width: 319px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .actions {
        margin-bottom: 20px;

        @media (max-width: $bp-laptop) {
            display: flex;
            justify-content: center;
            align-items: center;
        }

    }



}